}

.zmdi.pull-right {
  float: right;
  margin-left: .15em;
}

.zmdi-hc-spin {
  animation: zmdi-spin 1.5s infinite linear;
}

.zmdi-hc-spin-reverse {
  animation: zmdi-spin-reverse 1.5s infinite linear;
}

@keyframes zmdi-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}

@keyframes zmdi-spin-reverse {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-359deg);
  }
}

.zmdi-hc-rotate-90 {
  transform: rotate(90deg);
}

.zmdi-hc-rotate-180 {
  transform: rotate(180deg);
}

.zmdi-hc-rotate-270 {
  transform: rotate(270deg);
}

.zmdi-hc-flip-horizontal {
  transform: scale(-1, 1);
}

.zmdi-hc-flip-vertical {
  transform: scale(1, -1);
}

.zmdi-hc-stack {
  position: relative;
  display: inline-block;
  width: 2em;
  height: 2em;
  line-height: 2em;
  vertical-align: middle;
}

.zmdi-hc-stack-1x, .zmdi-hc-stack-2x {
  position: absolute;
  left: 0;
  width: 100%;
  text-align: center;
}

.zmdi-hc-stack-1x {
  line-height: inherit;
}

.zmdi-hc-stack-2x {
  font-size: 2em;
}

.zmdi-hc-inverse {
  color: #FFFFFF;
}

