html, body {
  height: 100%;
}

body {
  font-family: "Roboto", sans-serif;
  padding: 0;
  margin: 0;
}

.login-wrapper {
  /*background: url(../img/bg-login.jpg);
    background-size: cover;*/
}

.login-wrapper, .login-wrapper > .container, .login-wrapper .el-row {
  height: 100%;
}

.navbar-header .brand-title {
  color: #fff;
  text-align: center;
  line-height: 60px;
  font-size: 22px;
  background: #188AE2;
  letter-spacing: .05em;
}

.el-menu.sm-menu {
  border-radius: 0;
  color: #fff;
}

.el-menu.sm-menu .zmdi {
  margin-right: 10px;
}

.el-menu.sm-menu.sm-menu-right .el-submenu__icon-arrow {
  display: none;
}

.el-menu.sm-menu.sm-menu-right > .el-submenu, .el-menu.sm-menu.sm-menu-right > .el-menu-item {
  float: right;
}

.el-menu.sm-menu.sm-menu-right > .el-submenu .el-menu, .el-menu.sm-menu.sm-menu-right > .el-menu-item .el-menu {
  right: 5px;
  left: auto;
}

.el-input > .el-icon-zdmi {
  font-family: "Material-Design-Iconic-Font" !important;
}
